.home{
    padding-top: 171px;
    p{
        font-weight: 500;
        font-size: 48px;
        line-height: 68px;
        color: #FFFFFF;
        position: relative;
        top: -145px;
        text-align: center;
    }   
    .cards{
        background: #F7F7F7;
        width: 100%;
        max-width: 1240px;
        margin: auto;
        border-radius: 25px;
        margin-top: 43px;
        &__grid{
            display: grid;
            grid-template-columns: repeat(3, 1fr);
            grid-column-gap: 60px;
            grid-row-gap: 50px;
            padding: 50px;
        }
    }
}